import React from 'react';
import Breadcrumb from '../../Components/Breadcrumb';
import Servicedata from '../../Components/servieces/data';

const Services = (props) => {
  return (
    <>
      <Breadcrumb pagename={"Services"} />
      <div className="w3-services py-5">
        <div className="container py-lg-4">
          <div className="title-content text-left mb-lg-5 mb-4">
            <h6 className="sub-title">Our Services</h6>
            <h3 className="hny-title">Great Services</h3>
            <p>We are providing all Kind Of Visa And Immigration Services.</p>
          </div>
          {
            Servicedata.map((data, i) => {
              if (i % 2) {
                return (
                  <div key={i} className="row w3-services-grids"> 
                    <div className="col-lg-8 col-md-6 causes-grid">
                      <div className="causes-grid-info"><a id="airticket" href={"#"+data.AnchoreId} className="cause-title-wrap">&nbsp;</a>
                        <h4 className="cause-title">{data.Name}</h4>
                        <div dangerouslySetInnerHTML={{ __html: data.fullMessage }}></div>
                      </div>
                    </div>
                    <div className="col-lg-4 col-md-6 causes-grid">
                      <div className="causes-grid-info">
                         <img src={data.Image} className="img-fuild" alt={data.Name} />
                      </div>
                    </div>
                  </div>
                )
              } else {
                return (
                  <div key={i} className="row w3-services-grids"> 
                    <div className="col-lg-4 col-md-6 causes-grid hidden-xs">
                      <div className="causes-grid-info">
                         <img src={data.Image} className="img-fuild" alt={data.Name} />
                      </div>
                    </div>
                    <div className="col-lg-8 col-md-6 causes-grid">
                      <div className="causes-grid-info"><a id="airticket" href={"#"+data.AnchoreId} className="cause-title-wrap">&nbsp;</a>
                        <h4 className="cause-title">{data.Name}</h4>
                        <div dangerouslySetInnerHTML={{ __html: data.fullMessage }}></div>
                      </div>
                    </div>
                    <div className="col-lg-4 col-md-6 causes-grid hidden-sm hidden-md hidden-lg">
                      <div className="causes-grid-info">
                         <img src={data.Image} className="img-fuild" alt={data.Name} />
                      </div>
                    </div>
                  </div>
                )
              }
            })
          }
        </div>
      </div>
    </>
  )

}
export default Services