import React, { Component } from 'react';
import axios from 'axios';

export default class ContactusFrom extends Component {
    constructor(props) {
        super(props);

        this.state = { 
            //name: '', email: '', phone: '', message: '', 
            name: 'Rajesh', email: 'raj@gmail.com', phone: '1234567890', message: 'TEST PAGE',
            reciveremail: 'raju.b.chaudhari@gmail.com', 
            sent: false, statusView: 'Submit' 
        }

        this.formSubmit = this.formSubmit.bind(this);
        this.handleName = this.handleName.bind(this);
        this.handleEmail = this.handleEmail.bind(this);
        this.handlePhone = this.handlePhone.bind(this);
        this.handleMessage = this.handleMessage.bind(this);
        this.resetForm = this.resetForm.bind(this);
 
    } 

    // handle inputs
    handleName=(e)=>{ this.setState({ name:e.target.value }) }
    handlePhone=(e)=>{ this.setState({ phone:e.target.value }) }
    handleEmail=(e)=>{ this.setState({ email:e.target.value }) }
    handleMessage=(e)=>{ this.setState({ message:e.target.value }) }
    // end handle inputs

    formSubmit=(e)=> {
        e.preventDefault();   
        this.setState({statusView: 'Sending...'})             
        let data = JSON.stringify({
            name: this.state.name, 
            email:this.state.email, 
            phone:this.state.phone,
            message:this.state.message,
            reciveremail: this.state.reciveremail,
            sent:false
        });
        axios.post("/api/sendMail",data).then(res=>{
            //console.log("Message has been sent");
            this.setState({ statusView: 'Sending...'});
            this.resetForm();            
        }).catch((error)=>{            
            //console.log(error);            
            this.setState({ statusView: 'Submit'})
        });
    }
    
    //for reseting initial data
    resetForm=()=>{        
        this.setState({ sent:true});
        setTimeout(() => {            
            this.setState({ statusView:'Submit', sent:false });
            //this.setState({ name: '', email: '', phone: '',  message: '',  statusView:'Submit', sent:false });
        }, 3000);
    }

    render() {     
        return (            
            <>            
<form onSubmit={this.formSubmit} className="signin-form">    
    <div className="form-input">
        <input value={this.state.name} onChange={this.handleName} type="text" name="w3lName" id="w3lName" placeholder="Name" required="" />
    </div>
    <div className="row con-two">
        <div className="col-lg-6 form-input">
            <input onChange={this.handleEmail} value={this.state.email} type="email" name="w3lSender" id="w3lSender" placeholder="Email" required="" aria-describedby="emailHelp" />
        </div>
        <div className="col-lg-6 form-input">
            <input onChange={this.handlePhone} value={this.state.phone} type="text" name="w3lNumber" id="w3lNumber" placeholder="Phone number" className="contact-input" required="" />            
        </div>
    </div>
    <div className="form-input">
        <textarea onChange={this.handleMessage} value={this.state.message} placeholder="Message" name="w3lMessage" id="w3lMessage" required=""></textarea>
    </div>
    <div className="submit-button text-lg-right">
        <div className={this.state.sent?'text-lg-left float-lg-left msg msgAppear':'text-lg-left float-lg-left msg'}>Message has been sent</div>
        <button type="submit" className="btn btn-primary btn-style">{this.state.statusView}</button>
    </div>
</form>
            </>
        )
    }
}