import React, { Component } from 'react'
class Breadcrumb extends Component {
    render(props){
        return (
            <section className="w3l-about-breadcrumb position-relative text-center">
                <div className="breadcrumb-bg breadcrumb-bg-about py-sm-5 py-4">
                    <div className="container py-lg-5 py-3">
                        <h2 className="title">{this.props.pagename}</h2>
                        <ul className="breadcrumbs-custom-path mt-2">
                            <li><a href="./">Home</a></li>
                            <li className="active"><span className="fa fa-angle-double-right mx-2" aria-hidden="true"></span> {this.props.pagename}</li>
                        </ul>
                    </div>
                </div>
            </section>
        )
    }
}
export default Breadcrumb