import React from 'react';
import Datalist from './data';

const PackageBox = (props) => {
    return (
        <>
            <div className="row t-in">
            {
                Datalist.packages.map((data, i) => {
                    return (                                 
                        <div key={i} className="col-lg-4 col-md-6 price-main-info mt-md-0 mt-4">
                        <div className={(data.recommended?'price-inner card box-shadow active' : 'price-inner card box-shadow')}>
                            <div className="card-body">        
                                {
                                    data.recommended ? <label className="price-label">Recommended</label> : ""
                                }                                                        
                                <h4 className="text-uppercase text-center mb-3">{data.name}</h4>
                                <h5 className="card-title pricing-card-title"><span className="align-top">{data.currancy}</span>{data.price} </h5>
                                <ul className="list-unstyled mt-3 mb-4">
                                    {
                                        data.packagelist.map((list,i)=>{
                                            return (
                                                <li key={i}><span className="fa fa-check"></span>{list}</li>
                                            )
                                        })
                                    }
                                </ul>
                                {
                                    data.button.map((but,i)=>{
                                        return(
                                            <div key={i} className="read-more mt-4 pt-lg-2">
                                                <a href={but.url} className="btn btn-style btn-primary" alt="Gold Package £ 1299">{but.name}</a>
                                            </div>
                                        )
                                    })
                                }                                
                            </div>
                        </div>
                    </div>
                    )
                })
            }
            </div>

        </>
    )

}

export default PackageBox