import React from 'react';
import { NavLink } from 'react-router-dom';
import ScrolltoTop from '../ScrollTop';
const PathDir= process.env.PUBLIC_URL;

const Footer = (props) => {
  return(
      <footer className="w3l-footer-66">
          <section className="footer-inner-main">
              <div className="footer-hny-grids py-5">
                  <div className="container py-lg-4">
                      <div className="text-txt">
                          <div className="right-side">
                              <div className="row sub-columns">
                                  <div className="col-lg-4 col-md-6 sub-one-left pr-lg-4">
                                      <div className="foot-logo"><NavLink className="navbar-brand" to={PathDir}></NavLink></div>
                                      <div className="columns-2">
                                          <ul className="social">
                                              <li><a href="#facebook"><span className="fa fa-facebook" aria-hidden="true"></span>&nbsp;</a></li>
                                              <li><a href="#linkedin"><span className="fa fa-linkedin" aria-hidden="true"></span>&nbsp;</a></li>
                                              <li><a href="#twitter"><span className="fa fa-twitter" aria-hidden="true"></span>&nbsp;</a></li>
                                          </ul>
                                      </div>
                                  </div>
                                  <div className="col-lg-4 col-md-6 sub-one-left">
                                      <h6>Our Services</h6>
                                      <div className="mid-footer-gd sub-two-right">
                                          <ul>
                                              <li><NavLink to={`${PathDir}/about-us`}><span
                                                  className="fa fa-angle-double-right mr-2"></span>About Us</NavLink></li>
                                              <li><NavLink to={`${PathDir}/service`}><span
                                                  className="fa fa-angle-double-right mr-2"></span>Services</NavLink></li>
                                              <li><NavLink to={`${PathDir}/package`}><span
                                                  className="fa fa-angle-double-right mr-2"></span>Packages</NavLink></li>
                                              <li><NavLink to={`${PathDir}/testimonials`}><span
                                                  className="fa fa-angle-double-right mr-2"></span>Testimonials</NavLink></li>
                                          </ul>
                                          <ul>
                                              <li><NavLink to={`${PathDir}/contact-us`}><span
                                                  className="fa fa-angle-double-right mr-2"></span>Contact Us</NavLink></li>
                                          </ul>
                                      </div>
                                  </div>
                                  <div className="col-lg-4 col-md-6 sub-one-left">
                                      <h6>Contact Info</h6>
                                      <div className="sub-contact-info">
                                          <p>Address:20-22 Wenlock Road, London,<br />N1 7GU, United Kingdom.</p>
                                          <p className="my-3">Phone:<strong><a href="tel:+447411111212">+44 7411
                                                111212</a></strong></p>
                                          <p>E-mail:<strong><a
                                              href="mailto:info@1ststepinuk.co.uk">info@1ststepinuk.co.uk</a></strong>
                                          </p>
                                      </div>
                                  </div>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
              <div className="below-section">
                  <div className="container">
                      <div className="copyright-footer">
                          <div className="columns text-lg-left">
                              <p>© 2020 1st Step In UK. All rights reserved | Designed by <a
                                  href="https://w3layouts.com">W3layouts</a></p>
                          </div>
                          <ul className="columns text-lg-right">
                              <li><a href="privacy.html">Privacy Policy</a></li>
                              <li>|</li>
                              <li><a href="terms.html">Terms Of Use</a></li>
                          </ul>
                      </div>
                  </div>
              </div>
              {/*TOP Scroll*/}
              <ScrolltoTop />
          </section>
      </footer>
   )

 }

export default Footer