import React, { Component } from 'react';
import './style.css';

class Testimonialbox extends Component {
    render(props) {   
        return (
            <div className="item">
                <div className="slider-info mt-lg-4 mt-3">
                    <div className="img-circle-box">
                        <div className="img-circle-boxshadow"></div>
                        <div className="img-circle">
                            <img src={this.props.tstimg} className="img-fluid rounded testimonial_icon1" alt={this.props.tstalt} />
                        </div>
                    </div>
                    <blockquote className="blockquote my-5">
                        <div className="icon-quote"><span className="fa fa-quote-left" aria-hidden="true"></span></div>
                        <div className="message" dangerouslySetInnerHTML={{__html:this.props.tstshortmessage}}></div>
                        <footer className="blockquote-footer mt-3">{this.props.tstname}</footer>
                    </blockquote>
                </div>
            </div>
        )
    }
}

export default Testimonialbox