import React, { Component } from 'react'
import Breadcrumb from '../Breadcrumb';

class Userdetails extends Component {    
    constructor(){
        super();
        this.state = {
            users: []
        }
    }
    componentDidMount(){
        fetch('/users')
        .then(res => res.json())
        .then(users => this.setState({users}, () => console.log('Users fetched..', users)));
    }
    render() {
        return (
            <>
            <Breadcrumb pagename={"User Detailes"} />
            
      <div className="w3-services py-5">
        <div className="container py-lg-4">
          <div className="title-content text-left mb-lg-5 mb-4">
            <h6 className="sub-title">Detailes</h6>
          </div>
            <ul>
                {this.state.users.map(user =>
                    <li key={user.id}> { user.username }</li>
                )}
            </ul>
            
        </div>
      </div>
            </>
        )
    }
}

export default Userdetails;