import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
//import Menus from "../../Components/Menu/data";
const PathDir= process.env.PUBLIC_URL;

class Header extends Component {    
    render() {        
        return (
            <>
            <header id="site-header" className="fixed-top">
                <div className="header-topbox text-center">
                    <ul className="header-top-info">
                        <li><img alt="" src={`${PathDir}/assets/images/british-flag.gif`} className="flag-img" /></li>
                        <li><i className="fa fa-phone"></i><a href="tel:+447411111212">+44 7411 111212 </a></li>
                        <li><i className="fa fa-envelope"></i><a href="mailto:info@1ststepinuk.co.uk">info@1ststepinuk.co.uk</a></li>
                    </ul>
                </div>
                {/* <div className="site-headerbg"></div> */}
                <div className="container">
                    <nav className="navbar navbar-expand-lg navbar-light">
                        <div className="logo"><NavLink className="nav-link" to={PathDir}></NavLink></div><button className="navbar-toggler collapsed"
                            type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav"
                            aria-expanded="false" aria-label="Toggle navigation"><span className="fa icon-expand fa-bars"></span><span
                                className="fa icon-close fa-times"></span></button>
                        <div className="collapse navbar-collapse" id="navbarNav">
                            <ul className="navbar-nav">                               
                                <li className="nav-item" nav-rel="index"><NavLink className="nav-link" to={PathDir}>Home </NavLink></li>
                                <li className="nav-item" nav-rel="about"><NavLink className="nav-link" to={`${PathDir}/about-us`}>Aboutus</NavLink></li>
                                <li className="nav-item" nav-rel="services"><NavLink className="nav-link" to={`${PathDir}/service`}>Services</NavLink></li>
                                <li className="nav-item" nav-rel="packages"><NavLink className="nav-link" to={`${PathDir}/package`}>Packages</NavLink></li>
                                <li className="nav-item" nav-rel="testimonial"><NavLink className="nav-link" to={`${PathDir}/testimonials`}>Testimonial</NavLink></li>
                            </ul>
                            <ul className="navbar-nav search-right mt-lg-0 mt-2">
                                <li className="nav-item" nav-rel="contact"><NavLink to={`${PathDir}/contact-us`} className="btn btn-primary d-lg-block btn-style mr-2">Contact Us</NavLink></li>
                            </ul>
                            <div id="search" className="pop-overlay">
                                <div className="popup">
                                    <form action="#" method="GET" className="d-sm-flex">
                                        <input className="on-page-search" type="search" placeholder="Search.." name="search" required="required" autoFocus />
                                        <button type="submit">Search</button>
                                        <a className="close" href="#url">&times;</a>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div className="mobile-position">
                            <nav className="navigation">
                                <div className="theme-switch-wrapper">
                                    <label className="theme-switch" htmlFor="checkbox">
                                        <input type="checkbox" id="checkbox" />
                                        <div className="mode-container"><i className="gg-sun"></i><i className="gg-moon"></i></div>
                                    </label>
                                </div>
                            </nav>
                        </div>
                    </nav>
                </div>
            </header>
        </>
        )

    }
}

export default Header