import React, { Component } from 'react';
import Datalist from "./data";
 
class Infobox extends Component {
//const Infobox = (props) => {
    render(props){
    return (
        <>
        {/* <div className={this.props.classname}>
            <div className="blackbox">&nbsp;</div>
            <div className="bottomhny-gd-ingf">
                <h4 dangerouslySetInnerHTML={{__html: this.props.heading}} />
            </div>
        </div> */}
        {
            Datalist.map((data, i) => {
                return (
                    <div key={i} className={data.classname}>
                        <div className="blackbox">&nbsp;</div>
                        <div className="bottomhny-gd-ingf">
                            <h4 dangerouslySetInnerHTML={{__html: data.heading}} />
                        </div>
                    </div>
                )
            })
        } 
        </>
    )
    }
}
export default Infobox