import { Component } from 'react';
import './App.css';
import Footer from './Components/Footer';
import Header from './Components/Header';
import Contactus from './Containers/Contactus';
import Aboutus from './Containers/About';
import Services from './Containers/Services';
import Packages from './Containers/Packages';
import Testimonial from './Containers/Testimonial';
import Home from './Containers/Home';
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Error404 from './Error';
import Userdetails from './Components/User';
//import { connect } from 'react-redux', withRouter;
const PathDir= process.env.PUBLIC_URL;

class App extends Component{       
  render(){        
    return (
      <Router>
      <div className="App">    
      <div className="pageloader" id="pageloader"><div className="loading"><span className="dot"></span><div className="dots"><span></span><span></span><span></span></div></div></div>
        <Header />         
          <Switch>           
            <Route exact path={`${PathDir}/`} component={Home} />        
            <Route path={`${PathDir}/about-us`} component={Aboutus} />
            <Route path={`${PathDir}/service`} component={Services} />
            <Route path={`${PathDir}/package`} component={Packages} />
            <Route path={`${PathDir}/testimonials`} component={Testimonial} />
            <Route path={`${PathDir}/contact-us`} component={Contactus} />    
            <Route path={`${PathDir}/users`} component={Userdetails} />            
            <Route component={Error404} />
          </Switch>
        <Footer />
      </div>
      </Router>
    );
  }
}
export default App
//export default withRouter(connect(null, null)(App))
