import React, { Component } from 'react'
import './style.css';

class ServiceBox extends Component {
    render(props) {   
        return (
        <div className="item">
            <div className="slider-info">
                <div className="banner-info">
                    <div className="container">
                        <div className="features4-grid-inn">
                            <div className="img-featured">
                                <div className={'ch-item ch-img-' + this.props.idNumber}>
                                    <div className="ch-info-wrap">
                                        <div className="ch-info">
                                            <div className={'ch-info-front ch-img-' + this.props.idNumber}></div>
                                            <div className="ch-info-back">
                                                <h4>{this.props.heading}</h4>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="features4-rightinfo">
                                <h5><a href={this.props.anchore}>{this.props.heading}</a></h5>
                                <span dangerouslySetInnerHTML={{__html: this.props.message}}></span>               
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}

export default ServiceBox