import React from 'react';
import './style.css';
import Breadcrumb from '../../Components/Breadcrumb';
import Testimonialdata from '../../Components/Testimonial/data';

const Testimonial = (props) => {
  return (
    <>
    <Breadcrumb pagename={"Testimonial"} />
      <section className="w3l-testimonials w3l-testimonials-page py-5">
        <div className="testimonials py-4">
          <div className="container py-lg-4">
            <div className="title-content mb-lg-5 mb-4">
              <h6 className="sub-title">Client Testimonials</h6>
            </div>
            <div className="row">
              <div className="col-lg-12 mx-auto">
                <div className="owl-testimonial-page text-center owl-carousel owl-theme">
                  {
                    Testimonialdata.map((data, i) => {
                      return (
                        <div key={i} className="item">
                          <div className="slider-info mt-lg-4 mt-3">
                            <div className="img-circle">
                              <img src={data.testimonialImg} className="img-fluid rounded testimonial_icon1" alt={data.testimonialAlt} />
                            </div>
                            <div className="box box1 effect4">
                              <blockquote className="blockquote my-5">
                                <div className="icon-quote"><span className="fa fa-quote-left" aria-hidden="true"></span></div>
                                <div className="message" dangerouslySetInnerHTML={{ __html: data.testimonialShortMessage }}></div>
                                <footer className="blockquote-footer mt-3">{data.testimonialName}</footer>
                              </blockquote>
                            </div>
                          </div>
                        </div>
                      )
                    })
                  }
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )

}

export default Testimonial